<template>
  <div  class="my-clas">
   <div class="container pad1">
    <div class="row align-items-end text-center block-elephants-logo">
      <div class="logo-alephants col-md col-sm col-lg col-xl d-none d-sm-none d-md-inline d-lg-inline d-xl-inline">
        <img src="images/2.jpg" alt="elephant">
      </div>
      <div class="logo-alephants col-md col-sm col-lg col-xl d-sm-inline d-md-inline d-lg-inline d-xl-inline">
        <img src="images/1.jpg" alt="elephant">
      </div>
      <div class="logo-alephants col-md col-sm col-lg col-xl d-none d-sm-inline d-md-none d-lg-inline d-xl-inline">
        <img src="images/1.jpg" alt="elephant">
      </div>
      <div class="logo-alephants col-md col-sm col-lg col-xl d-none d-sm-none d-md-none d-lg-none d-xl-inline">
        <img src="images/1.jpg" alt="elephant">
      </div>
      <div class="logo-alephants col-md col-sm col-lg col-xl d-none d-sm-none d-md-inline d-lg-inline d-xl-inline">
        <img src="images/2.jpg" alt="elephant">
      </div>
    </div>
    <div class="row justify-content-md-center col-auto my-h-class">
      <h1>     ELEPHANTS</h1>
    </div>  
    <div class="row justify-content-center col-auto pad2">
        <button type="submit" name="#" value="#" data-toggle="modal" data-target=".bd-example-modal-sm"> </button>
    </div>
  </div>
  <v-footer class="my-foo"> 
    <div class="container">
    <div class="row justify-content-center align-items-center footer-my">
      <div class="col-auto">
        <p>Powered by</p>
      </div>
      <div class="col-auto">
        <img src="images/logo.jpg" alt="logo">
      </div>
    </div>
  </div>
 </v-footer>
   
  <!-- Modal -->
<div class="modal fade bd-example-modal-sm" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-sm">
    <div class="modal-content">
      <div class="l-auth">
      <v-form v-model="validLogin">
        <v-text-field label="Username"
                      v-model="credentials.username"
                      prepend-icon="account_box"
                      :rules="rules"
                      required
                      color="light-blue lighten-1">
        </v-text-field>

        <v-text-field label="Password"
                      v-model="credentials.password"
                      prepend-icon="lock"
                      :rules="rules"
                      :append-icon="loginPasswordVisible ? 'visibility' : 'visibility_off'"
                      :append-icon-cb="() => (loginPasswordVisible = !loginPasswordVisible)"
                      :type="loginPasswordVisible ? 'text' : 'password'"
                      color="light-blue lighten-1"
                      required>
        </v-text-field>
        <v-btn color="light-blue lighten-1" @click.native="submitAuthentication()"></v-btn>
      </v-form>
      <v-snackbar timeout="6000"
                bottom="bottom"
                color="red lighten-1"
                v-model="snackbar">
      {{ message }}
    </v-snackbar>
    </div>
    </div>
  </div>
</div>

 </div>
</template>
<script>
/*     index.js   Authentication,      authenticate,    .
snackbar:    .
validLogin:       .
validSignUp:     .
signUpVisible:      (   true).
loginPasswordVisible:   ,     ,      .
signUpPasswordVisible:   ,    ,    .
rules:   ,    .
credentials: ,        ,    .
newUser: ,        .
message:       .

     .   submitAuthentication    authenticate   Authentication,   ,      .*/
  import Authentication from '../../../components/pages/Authentication'
  export default {
    data () {
      return {
        snackbar: false,
        validLogin: false,
        loginPasswordVisible: false,
        rules: [ (value) => !!value || 'This field is required' ],
        credentials: {
          username: '',
          password: ''
        },
        message: ''
      }
    },
/*  submitAuthentication    authenticate   Authentication,   ,      . */
    methods: {
      submitAuthentication () {
        Authentication.authenticate(this, this.credentials, '/')
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "./../../../assets/styles";
.l-auth {
    background-color: $background-color;
    padding: 15px;
  }
.my-foo {
  background: none;
}
</style>


